import './ExpiryInfo.less'

import React from 'react'

export default class ExpiryInfo extends React.Component {
    renderInfo = () => ` 2 `

    render() {
        return (
            <div className="app-expiry-info">
                <span className="app-expiry-info__text">
                    {this.renderInfo()}
                </span>
            </div>
        )
    }
}



